import { z } from 'zod'

export const proposalSchema = z.object({
  // Client
  clientName: z.string().min(1, 'Nome do cliente  obrigatrio'),
  clientPhone: z.string().optional(),

  // Property
  propertyTitle: z.string().min(1, 'Ttulo do imvel  obrigatrio'),
  originalValue: z.number().min(0, 'Valor original  obrigatrio'),
  discountedValue: z.number().min(0, 'Valor com desconto  obrigatrio'),
  unit: z.string().min(1, 'Unidade  obrigatria'),
  area: z.string().min(1, 'Metragem  obrigatria'),
  address: z.string().min(1, 'Endereo  obrigatrio'),

  // Items
  items: z.array(z.string().min(1, 'Item no pode ser vazio')).length(6),

  // Payment
  downPayment: z.number().min(0, 'Entrada  obrigatria'),
  installments: z.string().min(1, 'Nmero de parcelas  obrigatrio'),
  annualPayment: z.number().optional(),
  financing: z.number().optional(),

  // Broker
  brokerName: z.string().min(1, 'Nome do corretor  obrigatrio'),
  brokerCreci: z.string().min(1, 'CRECI  obrigatrio'),
  brokerPhone: z.string().min(1, 'WhatsApp do corretor  obrigatrio'),
  brokerImage: z.string().optional(),

  // Validity
  validity: z.date({ required_error: 'Data de validade  obrigatria' }),
})

export type ProposalFormValues = z.infer<typeof proposalSchema>

export interface Proposal extends ProposalFormValues {
  id: string
  createdAt: string
  generationId: string
  pdfUrl?: string
  gammaUrl?: string
}

export interface GammaGenerationResponse {
  id: string
  status: 'QUEUED' | 'IN_PROGRESS' | 'COMPLETED' | 'ERROR'
  output?: {
    pdf?: {
      url: string
    }
    gamma?: {
      url: string
    }
  }
}

export interface GammaError {
  message: string
  code?: string
}
